<template>
  <div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <van-cell
        v-for="item in resultList"
        :key="item.art_id"
        :title="item.title"
      />
    </van-list>
  </div>
</template>
<script>
import { searchResultApi } from "@/api/home";
export default {
  props: {
    searchvalue: String,
  },
  data() {
    return {
      resultList: [], //接口返回的数据
      loading: false,
      finished: false,
      page: 1, //页码
    };
  },

  methods: {
    //1、页面触底会触发，2、数据没有铺满一屏的时候触发
    async onLoad() {
      let obj = {
        page: this.page,
        per_page: 10,
        q: this.searchvalue,
      };
      try {
        let { data } = await searchResultApi(obj);
        if (data.data.results.length != 0) {
          //页码累加1
          this.page++;
          this.resultList = [...this.resultList, ...data.data.results];
          this.loading = false;
        } else {
          //数据已全部加载完毕
          this.finished = true;
        }
      } catch (error) {
        //关闭加载状态
        this.loading = false;
        console.log(error);
      }
    },
  },
};
</script>
<style scoped></style>
