<template>
  <div>
    <div>
      <van-cell title="历史记录">
        <span v-if="deleteShow" class="alldel" @click="$emit('deleteAll')"
          >全部删除</span
        >
        <span v-if="deleteShow" @click="deleteShow = !deleteShow">完成</span>
        <template #right-icon v-if="!deleteShow">
          <van-icon
            name="delete"
            @click="deleteShow = !deleteShow"
            class="search-icon"
          />
        </template>
      </van-cell>
      <van-cell
        :title="item"
        v-for="(item, index) in searchHistoryList"
        :key="index"
        @click="deleteFn(index)"
      >
        <template #right-icon>
          <van-icon name="close" v-if="deleteShow" class="search-icon" />
        </template>
      </van-cell>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    searchHistoryList: Array,
  },
  data() {
    return {
      deleteShow: false, //存储当前是否是删除状态  false是非删除状态，true是删除状态
    };
  },
  methods: {
    deleteFn(val) {
      //判断是不是删除状态
      // 1、删除状态，点击直接删除这条数据
      // 2、非删除状态，打开搜索结果页面，并对当前点击的数据进行搜索
      if (this.deleteShow) {
        this.searchHistoryList.splice(val, 1);
      } else {
        // 打开搜索结果页面并传递当前点击的数据
        this.$emit("openResult", this.searchHistoryList[val]);
        console.log(123);
      }
    },
  },
};
</script>
<style lang="less" scoped>
.custom-title {
  margin-right: 4px;
  vertical-align: middle;
}

.search-icon {
  font-size: 40px;
  line-height: inherit;
}
.alldel {
  margin-right: 20px;
}
</style>
