<template>
  <div>
    <van-cell
      icon="search"
      v-for="(item, index) in suggestionList"
      :key="index"
      @click="$emit('keywordFn', item)"
    >
      <template #title>
        <!-- {{ item }} -->
        <div v-html="textHtml(item)"></div>
      </template>
    </van-cell>
  </div>
</template>
<script>
import { searchSuggestionApi } from "@/api/home";
import lodash from "lodash";
export default {
  props: {
    searchvalue: String,
  },
  data() {
    return {
      times: "",
      suggestionList: [], //联想词列表
      // text: '<span style="color: red">关键词</span>',
    };
  },

  created() {},
  methods: {
    textHtml(val) {
      //使用正则表达式
      //通过正则表达式，匹配了一个a
      // return val.replace(/a/gi,`<span style="color: red">a</span>`);
      let hightStr = `<span style="color: red">${this.searchvalue}</span>`;
      //如果需要根据一个变量来创建正则表达式，两个斜线中间没法写变量
      //就手动new RegExp正则表达式的构造函数
      // RegExp(匹配模式字符串, 匹配模式);
      let res = new RegExp(this.searchvalue, "gi");
      return val.replace(res, hightStr);
      // return `<span style="color: red">${this.searchvalue}</span>`;
    },
  },
  watch: {
    searchvalue: {
      //思考：每次输入都会执行搜索，非常消耗网络资源？
      //解决：我们让代码慢一点执行，拿到用户最后一次的输入结果
      //小结：使用Lodash（JavaScript 实用工具库）里面的防抖函数debounce
      handler: lodash.debounce(async function (newVal) {
        try {
          let { data } = await searchSuggestionApi(newVal);
          if (data.data.options[0] != null) {
            this.suggestionList = data.data.options;
          }
        } catch (error) {
          console.log(error);
        }
      }, 500),
      //一打开这个组件就要显示联想词
      //页面进来就执行一次监听
      immediate: true,
    },
  },
};
</script>
<style scoped></style>
