<template>
  <div>
    <div>
      <van-search
        v-model="searchvalue"
        class="searchclose"
        show-action
        background="#3296fa"
        placeholder="请输入搜索关键词"
      >
        <template #action>
          <div @click="onSearch">取消</div>
        </template>
      </van-search>
    </div>
    <div>
      <!-- 搜索结果 -->
      <search-result-vue v-if="searchResultShow" :searchvalue="searchvalue" />
      <!-- 搜索联想词 -->
      <search-suggestion-vue
        v-else-if="searchvalue"
        :searchvalue="searchvalue"
        @keywordFn="keywordFn"
      />
      <!-- 搜索历史 -->
      <search-history-vue
        @openResult="keywordFn"
        @deleteAll="searchHistoryList = []"
        v-else
        :searchHistoryList="searchHistoryList"
      />
    </div>
  </div>
</template>
<script>
import searchHistoryVue from "./component/searchHistory.vue";
import searchResultVue from "./component/searchResult.vue";
import searchSuggestionVue from "./component/searchSuggestion.vue";
import { setItem, getItem } from "@/utils/storage";
export default {
  components: {
    searchHistoryVue,
    searchResultVue,
    searchSuggestionVue,
  },
  data() {
    return {
      searchvalue: "", //搜索框的值
      searchResultShow: false, //当有了搜索结果才进行展示
      searchHistoryList: getItem("SEARCHHISTORY") || [], //历史记录集合
    };
  },
  methods: {
    onSearch() {
      this.$router.push("/home");
    },
    //点击的关键词
    keywordFn(val) {
      this.searchvalue = val;
      // 1、搜索的数据应该插入数组的前面
      // 2、如果搜索重复的数据，应该删除掉旧的记录，并把重新搜索的结果插入到数组的前面
      // indexOf会返回当前数据在数组中的索引位置，如果不存在，则返回-1
      let num = this.searchHistoryList.indexOf(val);
      if (num != -1) {
        this.searchHistoryList.splice(num, 1);
      }
      // .push().unshift()?????
      this.searchHistoryList.unshift(val);

      //搜索结果组件显示
      this.searchResultShow = true;
    },
  },
  watch: {
    searchHistoryList: {
      handler(newVal) {
        setItem("SEARCHHISTORY", newVal);
      },
    },
    //实时监听输入框的变化，没有值的时候，关闭搜索结果
    searchvalue(newVal) {
      if (newVal.trim().length == 0) {
        this.searchResultShow = false;
      }
    },
  },
};
</script>
<style lang="less" scoped>
/deep/ .searchclose {
  .van-search__action {
    color: #fff;
  }
}
</style>
